<template>
  <v-container fluid>
    <v-flex>
      <v-card>
        <v-card-text>
          <v-tabs
            fixed-tabs
            background-color="primary"
            dark
            v-model="tab"
          >
            <v-tab key="tab-1">
              Procesos
            </v-tab>
            <v-tab key="tab-2" v-if="!jefejuridico">
              Formalización Contractual Preliminar
            </v-tab>
            <v-tab key="tab-3">
              Formalización Contractual
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item
              key="tab-1"
            >
              <principal />
            </v-tab-item>
            <v-tab-item
              key="tab-2"
              v-if="!jefejuridico"
            >
              <formulacionContPre />
            </v-tab-item>
            <v-tab-item
              key="tab-3"
            >
              <formulacionCont
                :jefejuridico.sync = "jefejuridico"
              />
            </v-tab-item>
          </v-tabs-items>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-container>
</template>
<script>
import { adminApp } from '@/services/api'
export default {
  name: 'formUsuario',
  components: {
    principal: () => import('../adminApp/PrincipalAdmin'),
    formulacionContPre: () => import('../adminApp/FormulacionContractualPre'),
    formulacionCont: () => import('../adminApp/FormulacionContractual')
  },
  data () {
    return {
      email: '',
      dataRegistro: '',
      codigo: '0',
      aceptaterminos: false,
      estadotxt: '',
      e1: -1,
      tab: null,
      jefejuridico: false
    }
  },
  methods: {
    async getBuscaJefeArea () {
      try {
        this.jefejuridico = false
        const { data } = await adminApp.userXArea({ area: 7 })
        if (data.data[0].userid === this.$session.get('usuarioid')) {
          this.jefejuridico = true
        }
      } catch (error) {
        console.log(error)
      } finally {
      }
    }
  },
  watch: {
  },
  mounted () {
    this.getBuscaJefeArea()
  }
}
</script>
